<template>
  <v-container
    id="dashboard-view"
    fluid
    tag="section"
  >
    <v-row>
      <v-col
        cols="12"
      >
        <material-card
          color="green"
          full-header
        >
          <template #heading>
            <v-tabs
              v-model="tabs"
              background-color="transparent"
              slider-color="white"
              class="pa-3"
              show-arrows
            >
              <v-tab class="mr-3 font-weight-bold text-button" href="#AccountList" @click="updateRoute('#AccountList')">
                {{ $vuetify.lang.t('$vuetify.account_list') }}
              </v-tab>
              <v-tab class="mr-3 font-weight-bold text-button" href="#AccountRightList" @click="updateRoute('#AccountRightList')">
                {{ $vuetify.lang.t('$vuetify.account_right_list') }}
              </v-tab>
              <v-tab class="mr-3 font-weight-bold text-button" href="#SchoolList" @click="updateRoute('#SchoolList')">
                {{ $vuetify.lang.t('$vuetify.school_list') }}
              </v-tab>                             
            </v-tabs>
          </template>
          <v-tabs-items
            v-model="tabs"
            background-color="transparent"
          >
            <v-tab-item id="AccountList" key="AccountList">                      
              <v-card-text>
                  <v-lazy :options="{ threshold: 0.5 }" min-height="200" transition="fade-transition">  
                    <AccountList />
                  </v-lazy>
              </v-card-text>             
            </v-tab-item>
            <v-tab-item id="AccountRightList" key="AccountRightList">         
              <v-card-text>
                <v-lazy :options="{ threshold: 0.5 }" min-height="200" transition="fade-transition"> 
                <AccountRightList />
                </v-lazy>
              </v-card-text>              
            </v-tab-item>
            <v-tab-item id="SchoolList" key="SchoolList">                        
              <v-card-text>
                <v-lazy :options="{ threshold: 0.5 }" min-height="200" transition="fade-transition"> 
                <SchoolList />
                </v-lazy>
              </v-card-text>             
            </v-tab-item>                         
          </v-tabs-items>                                      
        </material-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
  // Components
  import AccountList from '@/components/AccountList'
  import AccountRightList from '@/components/AccountRightList'
  import SchoolList from '@/components/SchoolList'

  export default {
    name: 'Account',
    components: { AccountList, AccountRightList, SchoolList },
    data: () => ({
      dialog: false,
      dialogDelete: false,
      tabs: '',
    }), 
    methods: {
      updateRoute (route) {
        this.$router.push({ path: route })
      }      
    },
    mounted() {
      this.tabs = this.$route.hash == '' ? 'AccountList' : this.$route.hash.replace('#', '');
    },     
  }
</script>